"use client"

/**
 * FacilitatorSection Component
 * 
 * Displays the role of Facilitators in the network.
 * 
 * JUNIOR DEV NOTE:
 * - We map over arrays (`data.responsibilities.items`) to render repetitive UI elements.
 *   This keeps the JSX clean and reduces code duplication.
 * - Dynamic icons are handled by `ICON_MAP` to avoid conditional logic in the render loop.
 */

import { ShieldCheck, Landmark, MessageSquareText, LucideIcon, Check } from "lucide-react"

// Mapping string names from data to actual React Components
// This allows the data file to store "ShieldCheck" as a string.
const ICON_MAP: Record<string, LucideIcon> = {
  ShieldCheck,
  Landmark,
  MessageSquareText,
}

interface FacilitatorSectionProps {
  data: {
    title: string
    description: string
    responsibilities?: {
      title: string
      items: Array<{ title: string; description: string; icon: string }>
    }
    benefits?: {
      title: string
      items: Array<{ title: string; description: string }>
    }
  }
}

export function FacilitatorSection({ data }: FacilitatorSectionProps) {

  return (
    <section className="py-24 relative bg-transparent border-t border-white/5">
      <div className="container mx-auto px-4 sm:px-6 lg:px-8 space-y-16">
        
        {/* Header: Left Aligned, Full Width */}
        <div className="w-full text-left space-y-6">
          <h2 className="text-4xl md:text-5xl font-bold text-white">
            {data.title}
          </h2>
          <p className="text-xl text-muted-foreground leading-relaxed w-full">
            {data.description}
          </p>
        </div>

        {/* Content Columns: Using CSS Grid for responsive layout */}
        <div className="grid md:grid-cols-2 gap-12 lg:gap-20">
          
          {/* Column 1: Responsibilities */}
          <div className="space-y-8">
            <h3 className="text-2xl font-bold text-white mb-6 text-center md:text-left">{data.responsibilities?.title}</h3>
            <div className="space-y-6">
              {data.responsibilities?.items.map((item, index) => {
                // Determine which icon to show, defaulting to ShieldCheck if not found
                const Icon = ICON_MAP[item.icon] || ShieldCheck
                 return (
                   <div key={index} className="flex gap-4 group">
                     <div className="shrink-0 h-6 w-6 rounded-full bg-primary/10 flex items-center justify-center border border-primary/20 mt-1">
                       <Icon className="h-3.5 w-3.5 text-primary" />
                     </div>
                     <div>
                        <h4 className="text-lg font-semibold text-white mb-1 group-hover:text-primary transition-colors">{item.title}</h4>
                        <p className="text-base text-white/60 leading-relaxed font-light">{item.description}</p>
                     </div>
                   </div>
                 )
              })}
            </div>
          </div>

          {/* Column 2: Benefits */}
          <div className="space-y-8">
            <h3 className="text-2xl font-bold text-white mb-6 text-center md:text-left">{data.benefits?.title}</h3>
            <div className="space-y-6">
              {data.benefits?.items.map((item, index) => (
                <div key={index} className="flex gap-4 group">
                  <div className="shrink-0 h-6 w-6 rounded-full bg-green-500/10 flex items-center justify-center border border-green-500/20 mt-1">
                    <Check className="h-3.5 w-3.5 text-green-400" />
                  </div>
                  <div>
                    <h4 className="text-lg font-semibold text-white mb-1 group-hover:text-green-400 transition-colors">{item.title}</h4>
                    <p className="text-base text-white/60 leading-relaxed font-light">{item.description}</p>
                  </div>
                </div>
              ))}
            </div>
          </div>

        </div>

      </div>
    </section>
  )
}
